body {
  height: 100vh;
  background-color: #080e26;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 0 40px;
  display: flex;
}

.search-form {
  padding: 48px;
  display: flex;
  position: relative;
}

.search-form input[type="text"] {
  color: #01f7ff;
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid rgba(0, 0, 0, 0);
  border-radius: 30px;
  outline: none;
  padding: 12px;
  font-size: 16px;
  transition: border-color .5s ease-out, box-shadow .5s ease-out;
  box-shadow: 0 3px 10px rgba(0, 0, 0, .5);
}

.search-form button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid rgba(0, 0, 0, 0);
  border-radius: 30px;
  outline: none;
  margin-left: 10px;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
  position: relative;
}

.search-form button[type="submit"]:before, .search-form input[type="text"]:before {
  content: "";
  opacity: 0;
  border: 2px solid #01f7ff;
  border-radius: 30px;
  transition: opacity .5s ease-out;
  position: absolute;
  top: -5px;
  bottom: -5px;
  left: -5px;
  right: -5px;
}

.search-form button[type="submit"]:hover, .search-form input[type="text"]:hover {
  border-color: #01f7ff;
  box-shadow: 0 3px 20px rgba(1, 255, 242, .714);
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 24px;
  display: flex;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid rgba(0, 0, 0, 0);
  border-radius: 30px;
  outline: none;
  margin-left: 10px;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
  position: relative;
}

.load-more:hover {
  border-color: #01f7ff;
  box-shadow: 0 3px 20px rgba(1, 179, 255, .7);
}

.load-more:hover:before {
  opacity: 1;
  animation: glowing-border 1s ease-out infinite alternate;
}

.load-more {
  align-self: center;
  margin-top: 20px;
}

.photo-card img {
  width: 100%;
  max-width: 360px;
  margin: 0 auto;
}

.photo-card .info {
  width: 360px;
}

.info {
  flex-wrap: nowrap;
  justify-content: space-around;
  gap: 20px;
  display: flex;
}

.info p {
  color: #fff;
  text-align: center;
}

.photo-card {
  background-color: rgba(255, 255, 255, .1);
  border: 2px solid rgba(0, 0, 0, 0);
  border-radius: 10px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  display: flex;
  position: relative;
  box-shadow: 0 3px 10px rgba(0, 0, 0, .5);
}

.photo-card:before {
  content: "";
  opacity: 0;
  border: 2px solid #01f7ff;
  border-radius: 30px;
  transition: opacity .5s ease-out;
  position: absolute;
  top: -5px;
  bottom: -5px;
  left: -5px;
  right: -5px;
}

.photo-card:hover {
  border-color: #01f7ff;
  box-shadow: 0 3px 20px rgba(1, 255, 242, .714);
}

